import React, { useEffect, useState } from "react";
import axios from "axios";

export const App = () => {
  const [messages, setMessages] = useState([]);
  const [isOpen, setIsOpen] = useState(false);
  const [modalMessage, setModalMessage] = useState("");

  console.log(messages);

  const findAllMessages = async () => {
    try {
      const response = await axios.get("http://127.0.0.1:4567/");
      setMessages(response.data);
      return response.data;
    } catch (err) {
      console.error(err);
      throw err;
    }
  };

  useEffect(() => {
    findAllMessages();
  }, []);

  const editMessage = (id, body) => {
    return axios.put("http://127.0.0.1:4567/messages/" + id, body);
  };

  const handleModal = (message) => {
    setIsOpen(true);
    setModalMessage(message);
  };

  const handleEditMessage = (e, id) => {
    e.preventDefault();
    const fData = new FormData(e.target);
    const newMessage = fData.get("message");
    editMessage(id, { message: newMessage })
      .then((response) => {
        setIsOpen(false);
        const newMessage = response.data[0];
        const updatedMessages = messages.map((message) => {
          if (message.id == newMessage.id) {
            return newMessage;
          }
          return message;
        });
        setMessages(updatedMessages);
      })
      .catch((error) => console.error("Updating message", error));
  };

  return (
    <div className="container">
      <h1>Messages</h1>
      <div>
        {messages.map((message) => {
          return (
            <div key={message.id} className="messageWrapper">
              <div>{message.message}</div>
              <button onClick={() => handleModal(message)}>
                <i className="fa-solid fa-pen-to-square"></i>
              </button>
            </div>
          );
        })}
      </div>
      {isOpen && (
        <Modal
          onClose={() => setIsOpen(false)}
          handleSubmit={handleEditMessage}
          message={modalMessage}
        />
      )}
    </div>
  );
};

export default App;

export const Modal = ({ onClose, message, handleSubmit }) => {
  return (
    <div className="modal">
      <div className="modalContent">
        <div className="btnWrapper">
          <button onClick={onClose} className="close">
            X
          </button>
        </div>
        <div className="header">Edit message:</div>
        <form onSubmit={(event) => handleSubmit(event, message.id)}>
          <label for="message">
            <input
              type="text"
              defaultValue={message.message}
              name="message"
              id="message"
            />
          </label>

          <input type="submit" className="submitBtn" />
        </form>
      </div>
    </div>
  );
};
